.header {
    padding: 50px;
}

.header .appName {
    display: block;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 5vw;
    letter-spacing: 15px;
    border-bottom: 5px solid #000;
    padding-bottom: 10px;
    text-shadow: 6px 0 0 rgba(0,0,0,0.2);
    color: #ff0063;
}

.header .appMenu {
    padding: 15px 0 10px;
}

.header .appMenu__link {
    display: inline-block;
    padding: 10px;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 200;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 0.5px;
    border-bottom: 3px solid currentColor;
}

.header .appMenu__link + .appMenu__link {
    margin-left: 15px;
}

.header .appMenu__link:hover {
    color: #ff0063;
}

.header .appMenu__link.is_selected {
    color: #ff0063;
}



